<template>
  <div>
    <div class="left pro-tree">
      <el-input placeholder="请输入内容" v-model="inputVal" size="small">
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>
      <TreeOrg :treeData="treeData" v-on:childByType="childByType"></TreeOrg>
    </div>
    <div class="left chart-wrap">
      <div class="margin-left">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="电" name="power"></el-tab-pane>
          <el-tab-pane label="水" name="water"></el-tab-pane>
          <el-tab-pane label="气" name="gas"></el-tab-pane>
          <el-tab-pane label="热" name="heat"></el-tab-pane>
        </el-tabs>
        <el-row :gutter="10" class="margin-bottom" type="flex" justify="space-around">
          <el-col :span="4">
            <div class="use-block hidden">
              <div class="padding-left all-wrap">
                <p><a href="/oversee/userList" class="t-val">132</a> <span class="t-text">家</span></p>
                <p class="t-text">检测响应用户</p>
              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="use-block hidden">
              <div class="padding-left all-wrap">
                <p><span class="t-val">868</span> <span class="t-text">台</span></p>
                <p class="t-text">检测响应设备</p>
              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="use-block hidden">
              <div class="padding-left all-wrap">
                <p><span class="t-val">14:28</span> <span class="t-text">时·分</span></p>
                <p class="t-text">开始响应时间</p>
              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="use-block hidden">
              <div class="padding-left all-wrap">
                <p><span class="t-val">762.16</span> <span class="t-text">兆瓦</span></p>
                <p class="t-text">控制电力需求</p>
              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="use-block hidden">
              <div class="padding-left all-wrap">
                <p><span class="t-val">558.81</span> <span class="t-text">兆瓦</span></p>
                <p class="t-text">实际响应量</p>
              </div>
            </div>
          </el-col>
        </el-row>
        <div class="padding">
          <div class="block-title">实际自动需求响应负荷曲线(兆瓦)</div>
          <div id="actualLoadLine"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  import TreeOrg from "../../../common/TreeOrg.vue";
  export default {
    data () {
      return {
        inputVal:'',
        treeData: [
          {
            id:1,
            label:"按站点查看",
            type:"site",
            children: [
              {
                id:11,
                label: "唐家湾直流微网",
                type:"site",
                children: [
                  {
                    id:21,
                    label: "系统图",
                    type:"site",
                  },
                  {
                    id:22,
                    label: "储能子系统",
                    type:"site",
                  },
                  {
                    id:23,
                    label: "光伏",
                    type:"site",
                  }
                ]
              },
              {
                id:12,
                label: "横琴多能网",
                type:"site",
              }
            ]
          },
          {
            id:2,
            label:"按类型查看",
            type:"type",
            children: [
              {
                id:11,
                label: "斗门区 (12)",
                type:"type",
                children: [
                  {
                    id:21,
                    label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)",
                    type:"type",
                  },
                  {
                    id:22,
                    label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)",
                    type:"type",
                  },
                  {
                    id:23,
                    label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)",
                    type:"type",
                  }
                ]
              },
              {
                id:12,
                label: "金湾区 (6)",
                type:"type",
              }
            ]
          }
        ],
        activeName:'power',
      }
    },
    mounted(){
      let that = this;
      var startDate = that.getFormatDate(new Date());
      var days = 1;
      that.data = that.generateOHLC(startDate,days);
      that.getCosts(days);
      that.getForecastResult();
    },
    methods:{
      childByType:function(parm){
        let that = this;
      },
      handleClick(tab, event) {
        var that = this;
//        if(tab.paneName == 'load'){
//          that.showload = true;
//          that.showinstant = false;
//        }else if(tab.paneName == 'instant'){
//          that.showload = false;
//          that.showinstant = true;
//        }
      },
      //格式化日期，格式YYYY-MM-DD
      getFormatDate(date) {
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        var currentdate = year + seperator1 + month + seperator1 + strDate;
        return currentdate;
      },
      generateOHLC(startDate,count) {
        var data = [];
        //var xValue = +new Date();
        var dateArr = startDate.split('-');
        var year = parseInt(dateArr[0]);
        var month = parseInt(dateArr[1]) - 1;
        var day = parseInt(dateArr[2]);
        var xValue = new Date(year,month,day).valueOf();
        //console.log(xValue);
        var minute = 30 * 60 * 1000;
        var baseValue = Math.random() * 12000;
        var boxVals = new Array(4);
        var dayRange = 1;

        for (var i = 0; i < (parseInt(count)) * 24 * 2; i++) {
          baseValue = baseValue + Math.random() * 20 - 10;

          for (var j = 0; j < 4; j++) {
            boxVals[j] = (Math.random() - 0.5) * dayRange + baseValue;
          }
          boxVals.sort();

          var idxRandom = Math.random();
          var openIdx = Math.round(Math.random() * 3);
          var closeIdx = Math.round(Math.random() * 2);
          if (closeIdx === openIdx) {
            closeIdx++;
          }
          var volumn = boxVals[3] * (1000 + Math.random() * 500);

          // ['open', 'close', 'lowest', 'highest', 'volumn']
          // [1, 4, 3, 2]
          data[i] = [
            echarts.format.formatTime("hh:mm", (xValue += minute)),
//          +boxVals[openIdx].toFixed(2), // open
//          +boxVals[3].toFixed(2), // highest
//          +boxVals[0].toFixed(2), // lowest
//          +boxVals[closeIdx].toFixed(2), // close
            //volumn.toFixed(0),
            //getSign(data, i, +boxVals[openIdx], +boxVals[closeIdx], 4) // sign
          ];
        }
        //console.log(data);
        return data;

        function getSign(data, dataIndex, openVal, closeVal, closeDimIdx) {
          var sign;
          if (openVal > closeVal) {
            sign = -1;
          } else if (openVal < closeVal) {
            sign = 1;
          } else {
            sign =
              dataIndex > 0
                ? // If close === open, compare with close of last record
                data[dataIndex - 1][closeDimIdx] <= closeVal ? 1 : -1
                : // No record of previous, set to be positive
                1;
          }

          return sign;
        }
      },
      getCosts(days){
        let that = this;
        var parms = {
          days:days
        }
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/cost/getForecastResult",parms)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.actualArr = data.actualArr;
              that.forecastArr = data.forecastArr;
              //console.log(data.actualArr);
              that.getForecastResult();

            }
          });
      },
      getForecastResult() {
        var that = this;
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById("actualLoadLine"));
        // 指定图表的配置项和数据
        var option = {
//        dataset: {
//          source: that.data
//        },
          color: ["#0A6EDA", "#13ae2c"],
          title: {
            text: ""
          },
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
              crossStyle: {
                color: "#999"
              }
            }
          },
          legend: {
            data: ["实际负荷"],
            right: 0
          },
          grid: {
            x: "45",
            y: "30",
            x2: "20",
            y2: "60"
          },
          xAxis: {
            type: "category",
            //X轴数字
            axisLabel: {
              color: "#2B2B2B"
            },
            //X轴刻度线
            axisTick: {
              show: false
            },
            //纵向坐标网格线
            splitLine: {
              show: true,
              lineStyle: {
                color: "#e6e8ed"
              }
            },
            //X轴坐标线
            axisLine: {
              lineStyle: {
                color: "#e6e8ed"
              }
            },
            boundaryGap: false,
            data: that.data
          },
          yAxis: {
            type: "value",
            name:'kW',
            //Y轴数字
            axisLabel: {
              color: "#2B2B2B"
            },
            //Y轴刻度线
            axisTick: {
              show: false
            },
            //横向坐标网格线
            splitLine: {
              lineStyle: {
                color: "#e6e8ed"
              }
            },
            //Y轴坐标线
            axisLine: {
              lineStyle: {
                color: "#e6e8ed"
              }
            }
          },
          dataZoom: [
            {
              type: "inside",
              start: 50,
              end: 100
            },
            {
              show: true,
              type: "slider",
              y: "90%",
              start: 50,
              end: 100
            }
          ],
          series: [
            {
              name: "实际负荷",
              type: "line",
              symbol: 'none',
              //stack: "总量",
              data:that.actualArr,
              markLine: {
                data: [
                  {type: 'average', name: '平均值'}
                ]
              }
            }
          ]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      },
    },
    components: {
      TreeOrg
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #actualLoadLine{
    height: 350px;
  }
</style>



